<template>
  <div class="avatar-cropper">
    <div class="avatar-cropper-overlay" v-if="dataUrl">
      <div class="avatar-cropper-mark"></div>
      <div class="avatar-cropper-container relative">
        <div class="material-icons avatar-cropper-close" @click.stop.prevent='cancel'>close</div>
        <div class="avatar-cropper-image-container">
          <img :src="dataUrl" ref="img"
            @load.stop="createCropper"
            @error="onImgElementError">
        </div>
        <div class="avatar-cropper-footer">
          <button @click.stop.prevent="submit" class="button-small" :style='{background: $green}'>Завантажити</button>
        </div>
      </div>
    </div>
    <input type="file" name='avatar'
      v-if="!file"
      :accept="cleanedMimes"
      :capture="capture"
      class="avatar-cropper-img-input"
      ref="input"
      @change="onFileInputChange">
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import mime from 'mime/lite'
import axios from 'axios'
export default {
  emits: [
    'update:modelValue',
    'submit',
    'error',
    'cancel',
    'changed',
    'uploading',
    'completed',
    'uploaded',
  ],
  props: {
    modelValue: { type: Boolean, default: false },
    file: { type: File },
    uploadHandler: { type: Function },
    uploadUrl: { type: String },
    uploadFileField: { type: String, default: 'file' },
    uploadFileName: { type: [String, Function] },
    cropperOptions: {
      type: Object,
      default() { return {
        aspectRatio: 1,
        autoCropArea: 1,
        viewMode: 1,
        movable: false,
        zoomable: false,
      }},
    },
    outputOptions: { type: Object },
    outputMime: { type: String, default: null },
    outputQuality: { type: Number, default: 0.9 },
    mimes: { type: String, default: 'image/png, image/gif, image/jpeg, image/bmp, image/x-icon' },
    capture: { type: String },
  },
  data() { return {
    cropper: undefined,
    dataUrl: undefined,
    fileName: undefined,
    mimeType: undefined,
  } },
  computed: {
    cleanedMimes() {
      if (!this.mimes)
        throw new Error('vue-avatar-cropper: mimes prop cannot be empty')
      return this.mimes.trim().toLowerCase()
    },
  },
  watch: {
    modelValue(value) {
      if (!value) return
      if (this.file) this.onFileChange(this.file)
      else this.pickImage()
      this.$emit('update:modelValue', false)
    },
  },
  mounted() { this.$emit('update:modelValue', false) },
  methods: {
    destroy() {
      if (this.cropper) this.cropper.destroy()
      if (this.$refs.input) this.$refs.input.value = ''
      this.dataUrl = undefined
    },
    submit() {
      this.$emit('submit')
      if (this.uploadUrl) this.uploadImage()
      else if (this.uploadHandler) this.uploadHandler(this.cropper)
      else this.$emit('error', { message: 'Не вдалося обробити файл' })
      this.destroy()
    },
    cancel() {
      this.$emit('cancel')
      this.destroy()
    },
    onImgElementError() {
      this.$emit('error', { message: 'Не вдалося завантажити файл' })
      this.destroy()
    },
    pickImage() { if (this.$refs.input) this.$refs.input.click() },
    onFileChange(file) {
      const correctType = this.cleanedMimes.split(', ').find((mime) => mime === file.type)
      if (!correctType) {
        this.$emit('error', { message: 'Некоректний формат файла' })
        return
      }
      if (file.size > 2 * 1024 * 1024) {
        this.$emit('error', { message: 'Розмір файла перебільшує 2МБ' })
        return
      }
      const reader = new FileReader()
      reader.onload = (e) => {
        this.dataUrl = e.target.result
      }
      reader.readAsDataURL(file)
      this.fileName = file.name || 'unknown'
      this.mimeType = file.type
      this.$emit('changed', { file, reader })
    },
    onFileInputChange(e) {
      if (!e.target.files || !e.target.files[0]) return
      this.onFileChange(e.target.files[0])
    },
    createCropper() {
      this.cropper = new Cropper(this.$refs.img, this.cropperOptions)
    },
    getFilename(blob) {
      const extension = mime.getExtension(blob.type)
      // Default logic
      // if (!this.uploadFileName) {
      //   let actualFilename = this.fileName
      //   const filenameParts = this.fileName.split('.')
      //   if (filenameParts.length > 1) actualFilename = filenameParts.slice(0, -1).join('.')
      //   return `${actualFilename}.${extension}`
      // }
      // User provided filename
      //if (typeof this.uploadFileName === 'string') 
      return `${this.uploadFileName}.${extension}`
    },
    uploadImage() {
      this.cropper.getCroppedCanvas(this.outputOptions).toBlob(
        async (blob) => {
          const form = new FormData()
          const access = localStorage.getItem('access')
          form.append('file', blob, this.getFilename(blob))
          const config = { headers: { auth: `Bearer ${access}` } }
          const res = await axios.post(this.uploadUrl, form, config)
          this.$emit('uploaded', res.data)
          //else this.$emit('error', { message: 'Image upload fail' })
        },
        this.outputMime || this.mimeType,
        this.outputQuality,
      )
    },
  },
}
</script>

<style lang="scss">
.avatar-cropper {
  .avatar-cropper-overlay {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99999;
  }
  .avatar-cropper-img-input { 
    display: none 
  }
  .avatar-cropper-mark {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
  }
  .avatar-cropper-container {
    background: #fffd;
    z-index: 9;
    border-radius: 4px;
    box-shadow: 1px 1px 5px rgba(100, 100, 100, 0.14);
    .avatar-cropper-image-container {
      position: relative;
      max-width: 400px;
      height: 300px;
    }
    img {
      max-width: 100%;
      height: 100%;
    }
    .avatar-cropper-footer {
      padding: 1em;
      display: flex;
      justify-content: center;
    }
  }
}
.avatar-cropper-close {
  z-index: 10;
  position: absolute;
  right: -.5em; top: -.5em;
  padding: 5px;
  border-radius: 50%;
  box-shadow: 0 0 10px 2px rgba(0,0,0,.3);
  font-size: 2em !important;
  background: #fff;
  color: #000;
}
</style>
