<template>
  <avatar-cropper v-model="cropperVisible" 
   :upload-url="avatarUploadURL" 
   upload-file-name='avatar'
   @uploaded='setAvatar'
   @error='showError'/>

  <div class="my-info-section">
    <label>
      <div class="avatar-wrapper relative" @click='showCropper()'>
        <img v-show='avatar' :src="avatar" alt class="my-avatar" ref='avatar'>
        <i v-if='!avatar' class="material-icons-outlined no-avatar">photo_camera</i>
        <!-- <i v-if='avatar' class="material-icons-outlined add-avatar" @click='showCropper = true'>add_a_photo</i> -->
      </div>
    </label>
    <div class="my-info">
      <div class="my-name">{{ username ?? 'Гість' }}</div>
      <div class="my-score">{{ userinfo }}</div>
    </div>
  </div>
  <div class="navigation-section">
    <router-link class="navigation-link" to="modules">Мої курси</router-link>
    <router-link class="navigation-link" to="stats">Статистика</router-link>
    <router-link class="navigation-link" to="/rating?limit=10" v-if="username">Рейтинг</router-link>
  </div>
</template>

<script>
import avatarCropper from '../components/avatar-cropper.vue'
export default {
  components: { avatarCropper },
  props: {
    userinfo: { type: String, required: false },
  },
  data() { return {
    cropperVisible: false,
    avatarUploadURL: `${process.env.VUE_APP_API_URL}/upload-avatar`,
    avatar: localStorage.getItem('avatar'),
    username: localStorage.getItem('username'),
    isAuthorized: Boolean(localStorage.getItem('access')),
  }},
  methods: {
    showCropper() {
      if (this.isAuthorized) this.cropperVisible = true
    },
    showError(data) {
      this.$store.commit('showMessage', { 
        color: '#ff4d4d',
        title: 'Помилка',
        icon: 'close', 
        text: data.message
      })
    },
    setAvatar(data) {
      localStorage.setItem('avatar', data.avatar)
      this.$refs.avatar.src = data.avatar
    }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
$mobile: 1021px;
input {
  opacity: 0;
  position: absolute;
}
.my-info-section {
  padding: 3em 5vmax 1em 5vmax;
  color: #fff;
  @media (max-width: $mobile) {
    margin: 0 auto; 
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .avatar-wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 6em;
    height: 6em;
    margin-right: 1.75em;
    vertical-align: middle;
    font-weight: normal;
    background: #fff3;
    border-radius: 50%;
    line-height: 100%;
    text-align: center;
  }
  .my-avatar {
    width: 100%;
    object-fit: cover;
    border-radius: 50%;
    &:hover { opacity: .85 }
  }
  .no-avatar {
    display: inline-block;
    font-size: 3.25em;
    color: #fffd;
    padding-top: 4px;
    &:hover { opacity: .85 }
  }
  .add-avatar {
    position: absolute;
    right: -10%;
    bottom: 0%;
    width: 1.5em;
    height: 1.5em;
    font-weight: 200;
    border-radius: 50%;
    background: #fff6;
    text-align: center;
    line-height: 1.45em;
    color: #fff;
  }
  .my-info {
    display: inline-block;
    vertical-align: middle;
  }
  .my-name {
    margin-bottom: 2vmin;
    font-size: 2.5em;
    font-weight: bold;
  }
  .my-score { 
    color: #fffa 
  }
}
.navigation-section {
  text-align: right;
  @media (max-width: $mobile) { 
    text-align: center;
    margin-top: 4vmin;
    display: flex;
    * { flex-basis: 100%; }
  }
  .navigation-link {
    display: inline-block;
    width: 20ch;
    line-height: 2em;
    text-align: center;
    text-decoration: none;
    color: white;
    border-bottom: 2px solid #fff;
  }
  .navigation-link.router-link-active { 
    color: orange;
    border-bottom: 4px solid orange;
  }
}
</style>